.mainHeading{display: flex; align-items: center; right: 0;}
.categoryAdd {
    background: #266e7b;
    color: #fff;
    font-size: 12px;
    box-shadow: none;
}

.btn:hover{color: #fff; box-shadow: 0 5px 11px 0 rgba(0,0,0,0.10);}

.categoryList.card{
	padding: 30px;
    background: #fff;
    border-radius: 20px;
}

.MuiTableCell-head{
	background: #f2f7f8 !important;
}

.MuiToolbar-gutters {padding: 0; }
.MuiToolbar-regular {
    min-height: 32px;
}
.MuiPaper-root.MuiPaper-elevation4.MUIDataTable-paper-2.MuiPaper-rounded{    box-shadow: none; border: 1px solid #e8edf2; background: #ffffff;}
.MuiTableCell-head {background: rgb(255,255,255); background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(249,251,253,1) 100%); background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(249,251,253,1) 100%); background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(249,251,253,1) 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f9fbfd',GradientType=0 ); border: 1px solid #e8edf2; padding: 10px 15px; }
h6.MuiTypography-root {font-size: 14px; color: #000; }
.MuiTableCell-root {padding:10px 15px !important; }
.MuiTableRow-root.MuiTableRow-hover:hover {background-color: #ffffff !important; }

.oep-card .MuiToolbar-regular{    min-height: auto;}

.oep-card .MuiTableCell-body p{margin-bottom: 0;} 
h6.MuiTypography-root {font-size: 16px; color: #000; font-weight: bold !important; }
.imagePreview { width: 100px; height: 100px; }
.imagePreview img { width: 100%; height: 100%;}
.categoryList .categoryAdd { position: relative !important;  }

.card-body {
    flex: 1 1 auto;
    padding: 0rem;
}